import { useEffect, useState } from 'react';

export default function FeedbackForm() {
  let validators = {
    name: /^([a-z-]+[\s]{0,1}[a-z-]+[\s]{0,1}[a-z-]+)$/gi,
    email:
      /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu,
    phone: /(?:\+|\d)[\d\-\(\) ]{9,}\d/g,
  };
  ///^([a-z-]+[\s]{0,1}[a-z-]+[\s]{0,1}[a-z-]+)$/ig

  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [text, setText] = useState('');

  const [nameLabel, setNameLable] = useState('');
  const [phoneLabel, setPhoneLable] = useState('');
  const [emailLabel, setEmailLabel] = useState('');

  const onChangeName = (e) => {
    setName(e.target.value);
    if (!validators.name.test(String(e.target.value).toLowerCase())) {
      setNameLable(' ,  -   ');
    } else {
      setNameLable('');
    }
  };

  const onChangePhone = (e) => {
    setPhone(e.target.value);
    if (!validators.phone.test(String(e.target.value).toLowerCase())) {
      setPhoneLable(' ,  - 8 999 777 33 22');
    } else {
      setPhoneLable('');
    }
  };

  const onChangeEmail = (e) => {
    setEmail(e.target.value);
    if (!validators.email.test(String(e.target.value).toLowerCase())) {
      setEmailLabel('Email ');
    } else {
      setEmailLabel('');
    }
  };

//   const handleSubmit = (e) => {
//     e.preventDefault();

//   //   name.length
//   //     ? phone.length
//   //       ? email.length
//   //         ? send
//   //         : setEmailLabel('Email ')
//   //       : setPhoneLable(' ,  - 8 999 777 33 22')
//   //     : setNameLable(' ,  -   ');
//   // };
//   send(event, 'send.php')
// }
const form = document.getElementById('requestForm')

const handleSubmit = e => {
  e.preventDefault();
  let formData = new FormData(form);
  formData.append("name", name);
  formData.append("phone", phone);
  formData.append("email", email);
  formData.append("text", text);
  console.log(formData);
  fetch('send.php', {
    method: "POST",
    body: formData,
    headers: {
      "Content-Type": "multipart/form-data"
    }
  }).then(response => {
    response.json().then(data => {
      console.log("Successful" + data);
    });
  });
}

  // function send(event, php){
  //   //console.log(" ");
  //   event.preventDefault ? event.preventDefault() : event.returnValue = false;
  //   var req = new XMLHttpRequest();
  //   req.open('POST', php, true);
  //   req.onload = function() {
  //     if (req.status >= 200 && req.status < 400) {
  //     let json = JSON.parse(this.response); //  internet explorer 11
  //        console.log(json);
            
  //         //        
  //         if (json.result == "success") {
  //           //   
  //           alert(" ");
  //         } else {
  //           //   
  //           alert(".   ");
  //         }
  //       //      php 
  //       } else {alert(" . : "+req.status);}}; 
    
  //   //     .    
  //   req.onerror = function() {alert("  ");};
  //   req.send(new FormData(event.target));
  //   }
  //(e) => send(e, 'send.php')
  return (
    <div className="feedback-form">
      <div className="text">     </div>
      <form id="requestForm" method="post" onSubmit={handleSubmit}>
        <div className="form-row">
          <div className="input-data">
            <input
              name='name'
              value={name}
              onChange={(e) => onChangeName(e)}
              type="text"
              className="text"
            />
            <div className="underline"></div>
            <label htmlFor="">{nameLabel}</label>
          </div>
          <div className="input-data">
            <input
            name='phone'
              value={phone}
              onChange={(e) => onChangePhone(e)}
              type="text"
              className="text"
            />
            <div className="underline"></div>
            <label htmlFor="">{phoneLabel}</label>
          </div>
        </div>
        <div className="form-row">
          <div className="input-data">
            <input
            name='email'
              value={email}
              onChange={(e) => onChangeEmail(e)}
              type="text"
              className="text"
            />
            <div className="underline"></div>
            <label htmlFor="">{emailLabel}</label>
          </div>
          <div className="form-row">
            <div className="input-data textarea">
              <textarea name='text' cols="80" rows="8" onChange={e => setText(e.target.value)}></textarea>
              <br />
              <div className="underline"></div>
              <label htmlFor=""> </label>
              <br />
              <div className="form-row submit-btn">
                <div className="input-data">
                  <div className="inner"></div>
                  <input type="submit" value="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

